import { Component, OnInit } from '@angular/core';
import { ToastController, AlertController } from '@ionic/angular';
import { Sessao } from 'src/app/models/sessao';
import { NavExtrasService } from 'src/app/services/nav-extras.service';
import { SessaoService } from 'src/app/services/sessao.service';

@Component({
  selector: 'app-sessao-geral',
  templateUrl: './sessao-geral.page.html',
  styleUrls: ['./sessao-geral.page.scss'],
})
/**
 * Lista quais Pacientes estiveram presentes na sessão e a situação deles. 
 */
export class SessaoGeralPage implements OnInit {

  sessao:Sessao;

  constructor(public toastController: ToastController, private navExtra:NavExtrasService, private sessaoService:SessaoService, private alertController:AlertController) { }

  ngOnInit() {
    this.sessao = this.navExtra.get('sessao', null, false);
    console.log(this.sessao);
  }


  async salvar() {
    let alert = await this.alertController.create({
      message:'Solicitar que pacientes respondam um questionário sobre a sessão?',
      buttons:[
        {text: 'Não solicitar', handler:() => { this.sessao.disponivel = false; }},
        {text: 'Solicitar', handler:() => { this.sessao.disponivel = true; }},
      ]
    });
    alert.present();

    alert.onDidDismiss().then(() => {
      console.log(this.sessao)
      this.sessaoService.atualizar(this.sessao);
      this.toastController.create({
        message: 'Salvo com sucesso',
        duration: 3000
      }).then(t => t.present());
  
    })
  }
}
